<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="8">
          <b-card-group>
            <b-card no-body class="p-4">
              <b-card-body>
                <b-form>
                  <h1>系统登录</h1>
                  <p class="text-muted">欢迎使用商家后台</p>
                  <b-input-group class="mb-3">
                    <b-input-group-prepend>
                      <b-input-group-text><i class="icon-user"></i></b-input-group-text>
                    </b-input-group-prepend>
                    <b-form-input type="text" class="form-control" placeholder="请输入可以用于登录后台的用户名"
                                  autocomplete="username" v-model="username"/>
                  </b-input-group>
                  <b-input-group class="mb-4">
                    <b-input-group-prepend>
                      <b-input-group-text><i class="icon-lock"></i></b-input-group-text>
                    </b-input-group-prepend>
                    <b-form-input type="password" class="form-control" placeholder="请输入密码"
                                  autocomplete="current-password" v-model="password"/>
                  </b-input-group>
                  <b-row>
                    <b-col cols="6">
                      <b-button variant="primary" class="px-4" @click="login">登录</b-button>
                    </b-col>
                    <b-col cols="6" class="text-right">
                    </b-col>
                  </b-row>
                </b-form>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Login',
    data: function(){
        return {
          username: '',
          password: ''
        }
    },

    methods: {
        login(){
            console.log(this.username);
            console.log(this.password);
            if (!this.username || !this.password) {
                alert('请输入用户名或密码');
                return ;
            }

            this.$ajax('account', {
                method: 'login',
                usr: this.username,
                pwd: this.password
            }, function(res){
                console.log(res);
                if (res.code == 0) {
                    for(var i in res.data){
                        localStorage.setItem(i, res.data[i]);
                    }

                    window.location.hash = '/';
                } else {
                    alert(res.msg);
                }
            });
        }
    }
  }
</script>
